'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { Breadcrumb } from '@/components/SMBreadcrumb/SMBreadcrumb';
import { Card } from '@/components/common/SMCard/SMCard';
import { Loader2, ArrowRight } from 'lucide-react';
import { iconMap, IconName } from '@/utils/iconMapper';
import { NavigableServicesMenu } from '@/components/SMServices/SMNavigableServicesMenu';

interface ServiceCategory {
  id: number;
  name: string;
  slug: string;
  icon: string | null;
  description: string | null;
  parent_id: number | null;
  order: number;
  is_active: boolean;
  children?: ServiceCategory[];
  services?: Service[];
}

interface Service {
  id: number;
  title: string;
  subtitle: string;
  price: number;
  description: string;
  image_url: string;
  video_url: string;
}

export default function CategoryPage() {
  const params = useParams();
  const router = useRouter();
  const categorySlug = params.categorySlug as string;

  const [category, setCategory] = useState<ServiceCategory | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchCategory() {
      setLoading(true);

      const response = await fetch(`/api/service-categories/${categorySlug}`);
      const data: ServiceCategory = await response.json();

      // Если нет подкатегорий и есть ровно одна услуга - редиректим на неё
      const hasChildren = data.children && data.children.length > 0;
      const hasOnlyOneService = data.services && data.services.length === 1;

      if (!hasChildren && hasOnlyOneService) {
        router.push(`/services/${categorySlug}/${data.services[0].id}`);
        return;
      }

      setCategory(data);
      setLoading(false);
    }

    if (categorySlug) {
      fetchCategory();
    }
  }, [categorySlug]);

  // Показываем лоадер во время загрузки или если данных еще нет
  if (loading || !category) {
    return (
      <div className="flex min-h-screen bg-gray-50">
        <NavigableServicesMenu />
        <div className="flex-1">
          <Breadcrumb />
          <div className="flex items-center justify-center py-20">
            <Loader2 className="w-8 h-8 animate-spin text-[#18A36C]" />
          </div>
        </div>
      </div>
    );
  }

  const Icon = category.icon ? iconMap[category.icon as IconName] : null;
  const hasChildren = category.children && category.children.length > 0;
  const hasServices = category.services && category.services.length > 0;

  // Формируем breadcrumbs
  const breadcrumbItems = [
    { label: 'Услуги', href: '/services' },
    { label: category.name, href: `/services/${categorySlug}` }
  ];

  return (
    <div className="flex min-h-screen bg-gray-50">
      <NavigableServicesMenu />
      <div className="flex-1">
        <Breadcrumb items={breadcrumbItems} />
        <div className="p-4 lg:p-8">
          <div className="max-w-4xl mx-auto">
            {/* Header */}
            <div className="mb-8 lg:mb-12">
              <div className="text-center mb-6 lg:mb-8">
                {Icon && (
                  <div className="w-16 h-16 bg-[#18A36C] rounded-lg flex items-center justify-center mx-auto mb-4">
                    <Icon className="w-8 h-8 text-white" />
                  </div>
                )}
                <h1 className="text-2xl lg:text-3xl text-[#2E2E2E] mb-3 lg:mb-4">
                  {category.name}
                </h1>
                {category.description && (
                  <p className="text-gray-600 leading-relaxed text-sm lg:text-base">
                    {category.description}
                  </p>
                )}
              </div>
            </div>

            {/* Subcategories */}
            {hasChildren ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {category.children!.map((subCategory, index) => {
                  const SubIcon = subCategory.icon ? iconMap[subCategory.icon as IconName] : null;
                  const servicesCount = subCategory.services?.length || 0;
                  const childrenCount = subCategory.children?.length || 0;

                  return (
                    <div
                      key={subCategory.id}
                      className="group relative bg-white rounded-2xl border border-gray-200 hover:border-[#18A36C] transition-all duration-300 cursor-pointer hover:shadow-lg p-6"
                      onClick={() => router.push(`/services/${subCategory.slug}`)}
                    >
                      {/* Icon and Title */}
                      <div className="flex items-start gap-4 mb-4">
                        {SubIcon && (
                          <div className="flex-shrink-0 w-16 h-16 bg-[#18A36C]/10 rounded-xl flex items-center justify-center group-hover:bg-[#18A36C] transition-all duration-300">
                            <SubIcon className="w-8 h-8 text-[#18A36C] group-hover:text-white transition-colors duration-300" />
                          </div>
                        )}
                        <div className="flex-1 min-w-0">
                          <h3 className="text-lg font-semibold text-[#2E2E2E] group-hover:text-[#18A36C] transition-colors duration-300 mb-1">
                            {subCategory.name}
                          </h3>
                          {(childrenCount > 0 || servicesCount > 0) && (
                            <div className="flex items-center gap-3 text-xs text-gray-500">
                              {childrenCount > 0 && (
                                <span>{childrenCount} {childrenCount === 1 ? 'подкатегория' : 'подкатегорий'}</span>
                              )}
                              {servicesCount > 0 && (
                                <span>{servicesCount} {servicesCount === 1 ? 'услуга' : servicesCount < 5 ? 'услуги' : 'услуг'}</span>
                              )}
                            </div>
                          )}
                        </div>
                      </div>

                      {/* Description */}
                      {subCategory.description && (
                        <p className="text-sm text-gray-600 leading-relaxed line-clamp-2 mb-4">
                          {subCategory.description}
                        </p>
                      )}

                      {/* Arrow */}
                      <div className="flex justify-end">
                        <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center group-hover:bg-[#18A36C] transition-all duration-300">
                          <ArrowRight className="w-4 h-4 text-gray-600 group-hover:text-white transition-colors duration-300" />
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            ) : hasServices ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {category.services!.map((service) => (
                  <div
                    key={service.id}
                    className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-white to-gray-50 border border-gray-200 hover:border-[#18A36C] transition-all duration-300 cursor-pointer hover:shadow-xl hover:shadow-[#18A36C]/10"
                    onClick={() => {
                      // Переход на страницу услуги
                      // TODO: нужно определить правильный роут
                      router.push(`/services/${categorySlug}/${service.id}`);
                    }}
                  >
                    {/* Background decoration */}
                    <div className="absolute top-0 right-0 w-32 h-32 bg-[#18A36C]/5 rounded-full blur-3xl transform translate-x-16 -translate-y-16 group-hover:scale-150 transition-transform duration-500" />

                    <div className="relative">
                      {/* Image */}
                      <div className="h-48 overflow-hidden">
                        <img
                          src={service.image_url}
                          alt={service.title}
                          className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                        />
                      </div>

                      {/* Content */}
                      <div className="p-5">
                        <h3 className="text-lg font-semibold text-[#2E2E2E] group-hover:text-[#18A36C] transition-colors duration-300 mb-2 line-clamp-2">
                          {service.title}
                        </h3>
                        <p className="text-sm text-gray-600 leading-relaxed line-clamp-2 mb-3">
                          {service.subtitle}
                        </p>
                        <div className="flex items-center justify-between">
                          <span className="text-lg font-bold text-[#18A36C]">
                            {service.price} BYN
                          </span>
                          <div className="w-8 h-8 bg-[#18A36C]/10 rounded-full flex items-center justify-center group-hover:bg-[#18A36C] transition-all duration-300">
                            <ArrowRight className="w-4 h-4 text-[#18A36C] group-hover:text-white group-hover:translate-x-1 transition-all duration-300" />
                          </div>
                        </div>
                      </div>
                    </div>

                    {/* Bottom accent line */}
                    <div className="h-1 bg-gradient-to-r from-transparent via-[#18A36C]/20 to-transparent group-hover:via-[#18A36C] transition-all duration-300" />
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center bg-white border border-[#E8E6E3] rounded-lg p-8 lg:p-12">
                <p className="text-gray-600 mb-4">
                  Услуги в этой категории скоро появятся
                </p>
                <p className="text-sm text-gray-500">
                  Для получения информации свяжитесь с нами по телефону или через форму обратной связи
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
